<script setup>
import {ref, onMounted} from "vue";
import axios from "axios";

const courses = ref([])

onMounted(() => {
  fetchCourses()
})
async function fetchCourses() {
  try {
    let url = '/api/course/get_courses/'
    await axios.get(url).then(response => {
        courses.value = response.data.courses
      })
  } catch (error) {
    console.log(error)
  }
}

</script>

<template>
  <div>
    List of courses offered by the instution:
    <p v-for="item in courses" :key="item.id">
      {{ item.title }}
    </p>
  </div>
</template>

